@import "../../mixins.scss";

.search-results-container {
  position: fixed;
  width: 100%;
  height: 100%;
  background: var(--background);
  display: flex;
  flex-direction: column;

  .title-row {
    display: flex;
    gap: .25rem;
    align-items: center;

    & span {
      font-size: var(--font-size-small);
      color: var(--grey-4);
      flex: 1;

      &.target-group-name {
        color: var(--grey-5);
      }
    }

  }

  .message {
    text-align: center;
    padding: 2rem 0;
    color: var(--faded-white);
  }

  .spinner {
    display: flex;
    justify-content: center;
    padding-top: 2rem;
    color: var(--faded-white);
  }

  .results {
    flex: 1;
    min-height: 0;
    overflow-y: auto;
    padding: 0;
    padding-bottom: 2rem;
  }

  .search-bar {
    position: relative;
    display: flex;

    i {
      position: absolute;
      left: 1rem;
      top: 50%;
      transform: translateY(-50%);
      color: var(--grey-3);
    }

    input {
      padding-left: 3rem;
      flex: 1;
      min-width: 0;
      width: 100%;
    }
  }
}